
export class ContactModel {

	constructor(){
		
	}


	handle_contact_main_section1_form1_submit = (formData)=>{

		    if (process.env.NODE_ENV === "production") {
		return fetch("https://abbey-road-api.onrender.com/api/contact-page/send_email",{
			method:'post',
			headers: {
				"Content-Type" : "application/json"
			},
			body: JSON.stringify(formData)
		})
		.then((res)=>{
			return res.json();
		})
		.then((res)=>{
			if(res.status){
				return Promise.resolve({
					status:true})
			}
			else{
				console.log(res.msg)
				return Promise.reject({
					status: false
				})
			}
		})
		.catch((err)=>{
			console.log(err)
			return Promise.resolve({status:false})
		})
		console.log(formData);
		    }
		    else{
		return fetch("http://localhost:3001/api/contact-page/send_email",{
			method:'post',
			headers: {
				"Content-Type" : "application/json"
			},
			body: JSON.stringify(formData)
		})
		.then((res)=>{
			return res.json();
		})
		.then((res)=>{
			if(res.status){
				return Promise.resolve({
					status:true})
			}
			else{
				console.log(res.msg)
				return Promise.reject({
					status: false
				})
			}
		})
		.catch((err)=>{
			console.log(err)
			return Promise.resolve({status:false})
		})
		console.log(formData);
		    }


	}
	
	formattedDate = () => {

		const date = new Date();

		// Array of month short names
		const months = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", 
		                "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];

		const formattedDate = 
		    `${months[date.getMonth()]} ${date.getDate()} ${date.getFullYear()} ` +
		    `${String(date.getHours()).padStart(2, '0')}:` +
		    `${String(date.getMinutes()).padStart(2, '0')}:` +
		    `${String(date.getSeconds()).padStart(2, '0')}`;

		console.log(formattedDate);

		return formattedDate;
	}
}